header span {
  width: 50%;
  display: inline-block;
  box-sizing: border-box;
}
header span:first-child {
  text-align: left;
  padding-left: 30px;
}
header span:last-child {
  text-align: right;
  padding-right: 20px;
  padding-top: 10px;
}
img {
  width: 180px;
  height: 80px;
}
.search-field,
.tabs {
  display: inline-block;
}
.search-field input {
  height: 30px;
  border-width: thin;
}
.search-icon {
  position: relative;
  top: 10px;
  left: -25px;
}
